<!-- by 添少 -->
<template>
  <section>
    <v-Header class="head">
          <span slot='text' class="headText">上传封面</span>
          <mt-button class="nextBtn" @click="handNext" slot="right">下一步</mt-button>
        </v-Header>
    <section class="upload">
      <template v-if="!cover">
        <h3 class="fw4">上传封面</h3>
      </template>
      <template v-else>
        <h3 class="fw4">上传成功</h3>
      </template>
      <file-image2
        :src="cover"
        @load="handLoadFile"
        @delete="handDeleteImage">
      </file-image2>
      <p>注：该封面图片会出现在“{{contTypeName}}”的文章列表及文章头部</p>
    </section>
    <cropper-box v-show="cropper.dialog" :src="cropper.src" @cancel="cropper.dialog = false"
                 @done="handCropperDone"></cropper-box>
  </section>
</template>
<script>
  import Header from '../../components/header';
  import {mapState} from 'vuex'
  import FileImage2 from '@/components/file-image2'
  import CropperBox from '@/components/cropper-box'

  export default {
    name: '',
    components: {
      FileImage2,
      CropperBox,
       'v-Header': Header,
    },
    props: {},
    data() {
      return {
        cropper: {
          dialog: false,
          src: '',
        },
        detail: {}
      }
    },
    watch: {},
    computed: {
      ...mapState({
        ContType: state => state.config.ContType,
        userType: state => state.userInfo.userType,
        contType: state => state.write.article.contType,
        cover: state => state.write.article.cover,
      }),
      contTypeName() {
        return (this.ContType.find(e => e.value === this.contType) || {label: '文章'}).label
      },
    },
    created() {
      this.init()
    },
    methods: {
      init() {
        this.$store.commit(this.$actionType.set_article_data, {
          contType: parseInt(this.$route.query.contType),
        })
        if (this.$route.query.productId) {
          this.$http.get(this.$port.ARTICLE_PROT, {
            params: {
              productId: this.$route.query.productId
            }
          }).then(res => {
            if (res.code === 200) {
              this.$store.commit(this.$actionType.set_article_data, {
                productId: this.$route.query.productId,
                cover: res.data.preimg,
                title: res.data.title,
                desc: res.data.desc,
                content: res.data.cont,
              })
            } else {
              alert(res.msg)
            }
          })
        }
      },
      initData() {
      },
      handDeleteImage() {
        this.$store.commit(this.$actionType.set_article_data, {
          cover: ''
        })
      },
      handLoadFile(event, file) {
        // console.log(event, file)
        this.$commons.compressorImage(file).then(res => {
          this.cropper.dialog = true
          setTimeout(() => {
            this.cropper.src = window.URL.createObjectURL(res)
          }, 200)
        })
      },
      handCropperDone(cropper) {
        cropper.getCroppedCanvas({
          width: 500,
          height: 500
        }).toBlob(blob => {
          this.$commons.uploadImage(blob).then(res => {
            if (res.code === 200) {
              let contType = parseInt(this.$route.query.contType)
              if (isNaN(contType)) {
                contType = 1
              }
              this.$store.commit(this.$actionType.set_article_data, {
                cover: res.data,
                contType,
              })
              this.cropper.dialog = false
            } else {
              alert('图片上传失败')
            }
          })
        })
      },
      handNext() {
        if (!this.cover) {
          alert('请上传图片')
          return
        }
        if (this.userType === 4) {
          this.$router.push({
            path: '/write/club'
          })
        } else {
          this.$router.push({
            path: 'edit',
            query: this.$route.query
          })
        }
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>

  .upload {
    background-color: #fff;
    text-align: center;
    padding: 0 10px 10px;
    margin-top: 10px;
    h3 {
      font-size: 24px;
      font-weight: 400;
      line-height: 65px;
      padding: 0;
      margin: 0;
    }
    p {
      font-size: 14px;
      color: #adadad;
    }
    .ui-upload {
      margin-bottom: 10px;
    }
  }

  .ui-upload {
    width: 34.4vw;
    height: 26.4vw;
  }

  .uploadImage {
    width: 34.4vw;
    height: 26.4vw;
    margin-bottom: 10px;
    position: relative;
    border: solid #e5e5e5 1px;
    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: contain;
    }
    b {
      background-color: rgba(0, 0, 0, 0.5);
      text-align: center;
      font-weight: 400;
      font-size: 8px;
      color: #fff;
      width: 20px;
      height: 20px;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
</style>



